"use client";

import React, { useState, useEffect } from "react";
import {
  Input,
  Button,
  Select,
  SelectItem,
  Card,
  Modal,
  ModalContent,
  ModalHeader,
  ModalBody,
  ModalFooter,
  toast,
} from "@heroui/react";
import Image from "next/image";
import PhoneInput from "react-phone-input-2";
import { motion, AnimatePresence } from "framer-motion";
import "react-phone-input-2/lib/style.css";

interface DependantType {
  id: string | number;
  relationship: string;
  title: string;
  firstName: string;
  middleName: string;
  surname: string;
  idtypes: string;
  idnos: string;
  dob: string;
  gendere: string;
}

export interface FormdataType {
  policyScheme: string;
  relationship: string;
  staffId: string;
  title: string;
  firstname: string;
  middleName: string;
  lastname: string;
  idtype: string;
  idno: string;
  dateofbirth: string;
  gender: string;
  country: string;
  city: string;
  address: string;
  mobileno: string;
  eimail: string;
  dependantsData: DependantType[];
}

export interface ErrorsType {
  [key: string]: string;
}

const StaffMedicalForm: React.FC = () => {
  const today = new Date().toISOString().split("T")[0];

  const [formData, setFormData] = useState<FormdataType>({
    policyScheme: "Birdview Microinsurance Medical Scheme",
    relationship: "Principal",
    staffId: "",
    title: "",
    firstname: "",
    middleName: "",
    lastname: "",
    idtype: "",
    idno: "",
    dateofbirth: "",
    gender: "",
    country: "Kenya",
    city: "",
    address: "",
    mobileno: "",
    eimail: "",
    dependantsData: [
      {
        id: 1,
        relationship: "",
        title: "",
        firstName: "",
        middleName: "",
        surname: "",
        idtypes: "",
        idnos: "",
        dob: "",
        gendere: "",
      },
    ],
  });

  const [loaderIcon, setLoaderIcon] = useState(false);
  const [dependentCount, setDependentCount] = useState(1);
  const [errors, setErrors] = useState<ErrorsType>({});
  const [currentDependant, setCurrentDependant] = useState<DependantType | null>(null);
  const [openModal, setOpenModal] = useState(false);

  // Sync dependants count
  useEffect(() => {
    setFormData((prev) => {
      const newDependants = Array.from({ length: dependentCount }, (_, idx) => {
        return prev.dependantsData[idx] || {
          id: idx + 1,
          relationship: "",
          title: "",
          firstName: "",
          middleName: "",
          surname: "",
          idtypes: "",
          idnos: "",
          dob: "",
          gendere: "",
        };
      });
      return { ...prev, dependantsData: newDependants };
    });
  }, [dependentCount]);

  const handleChange = (
    e: React.ChangeEvent<HTMLInputElement | HTMLSelectElement>
  ) => {
    const { name, value } = e.target;
    setFormData((prev) => ({ ...prev, [name]: value }));
  };

  const handlePhoneChange = (value: string) => {
    setFormData((prev) => ({ ...prev, mobileno: value }));
  };

  const handleAddDependant = () => {
    if (formData.dependantsData.length >= 7) {
      toast({
        title: "⚠️ Limit reached",
        description: "You can only add up to 7 dependants.",
        variant: "warning",
      });
      return;
    }
    setDependentCount((prev) => prev + 1);
  };

  const handleOpenModal = (dep: DependantType) => {
    setCurrentDependant(dep);
    setOpenModal(true);
  };

  const handleCloseModal = () => {
    setCurrentDependant(null);
    setErrors({});
    setOpenModal(false);
  };

  const handleChangeDep = (
    e: React.ChangeEvent<HTMLInputElement | HTMLSelectElement> | { name: string; value: string }
  ) => {
    if ("target" in e) {
      const { name, value } = e.target;
      setCurrentDependant((prev) => prev && { ...prev, [name]: value });
      setErrors((prev) => ({ ...prev, [name]: "" }));
    } else {
      const { name, value } = e;
      setCurrentDependant((prev) => prev && { ...prev, [name]: value });
      setErrors((prev) => ({ ...prev, [name]: "" }));
    }
  };

  const handleSaveDependant = () => {
    if (!currentDependant) return;
    const newErrors: ErrorsType = {};
    ["relationship", "title", "firstName", "middleName", "surname", "idtypes", "idnos", "dob", "gendere"].forEach(
      (key) => {
        if (!(currentDependant as any)[key]) newErrors[key] = `${key} is required`;
      }
    );
    if (Object.keys(newErrors).length > 0) {
      setErrors(newErrors);
      return;
    }
    setFormData((prev) => ({
      ...prev,
      dependantsData: prev.dependantsData.map((d) =>
        d.id === currentDependant.id ? currentDependant : d
      ),
    }));
    handleCloseModal();
  };

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    setLoaderIcon(true);
    try {
      const res = await fetch("/api/medicalStaff_form", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify(formData),
      });
      const data = await res.json();
      if (res.ok) {
        toast({
          title: "✅ Success",
          description: data.message,
          color: "success",
          variant: "solid",
          placement: "top-right",
        });
        handleReset();
      } else {
        toast({
          title: "❌ Error",
          description: data.error,
          color: "danger",
          variant: "solid",
          placement: "top-right",
        });
      }
    } catch (err: any) {
      toast({
        title: "❌ Error",
        description: err.message,
        color: "danger",
        variant: "solid",
        placement: "top-right",
      });
    } finally {
      setLoaderIcon(false);
    }
  };

  const handleReset = () => {
    setFormData({
      policyScheme: "Birdview Microinsurance Medical Scheme",
      relationship: "Principal",
      staffId: "",
      title: "",
      firstname: "",
      middleName: "",
      lastname: "",
      idtype: "",
      idno: "",
      dateofbirth: "",
      gender: "",
      country: "Kenya",
      city: "",
      address: "",
      mobileno: "",
      eimail: "",
      dependantsData: [
        {
          id: 1,
          relationship: "",
          title: "",
          firstName: "",
          middleName: "",
          surname: "",
          idtypes: "",
          idnos: "",
          dob: "",
          gendere: "",
        },
      ],
    });
    setDependentCount(1);
  };

  return (
    <div
      className="min-h-screen flex items-center justify-center px-4 
             bg-[url('/images/form-bg.jpg')] bg-cover bg-center bg-no-repeat"
    >
      {loaderIcon && (
        <div className="fixed inset-0 z-50 flex items-center justify-center bg-white/70">
          <div className="animate-spin rounded-full h-12 w-12 border-b-2 border-blue-600"></div>
        </div>
      )}

      <Card className="w-full max-w-3xl shadow-xl rounded-2xl p-6 bg-white/90 backdrop-blur-md">
        <div className="flex justify-center my-4">
          <Image src="/images/logo.jpeg" alt="Logo" width={180} height={50} />
        </div>
        <h2 className="text-xl font-bold text-center mb-6">
          Staff Medical Detail Forms
        </h2>

        {/* ✅ your full form remains the same here */}
      </Card>
    </div>

  );
};

export default StaffMedicalForm;
